import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import About from '@/components/About'
import Skills from '@/components/Skills'
import Portfolio from '@/components/Portfolio'
import Services from '@/components/Services'
import Contact from '@/components/Contact'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'inicio',
      component: Home
    },
    {
      path: '/sobre-mi',
      name: 'sobre-mi',
      component: About
    },
    {
      path: '/habilidades',
      name: 'habilidades',
      component: Skills
    },
    {
      path: '/portafolio',
      name: 'portafolio',
      component: Portfolio
    },
    {
      path: '/servicios',
      name: 'servicios',
      component: Services
    },
    {
      path: '/contacto',
      name: 'contacto',
      component: Contact
    },

  ]
})



